import recipeData from "./recipeData";

const tags = recipeData.map((data) => {
  return data.tags;
});

const allTags = tags.flat();
const count = {};
allTags.forEach(function (i) {
  count[i] = (count[i] || 0) + 1;
});

const uniqueTags = [...new Set(allTags)];

const tagData = uniqueTags.sort().map((tag, index) => {
  return { id: index + 100, tag: tag, active: false, num: count[tag] };
});

export default tagData;
