import React from "react";

function Filter(props) {
  const check = props.filter.active ? (
    <i
      className="fas fa-check-square"
      onClick={(event) => props.handleOff(props.filter.id)}
    ></i>
  ) : (
    <i
      className="far fa-square"
      onClick={(event) => props.handleOn(props.filter.id)}
    ></i>
  );

  return (
    <div className="filter">
      {check}
      <p>{props.filter.tag}</p>
      <span>({props.filter.num})</span>
    </div>
  );
}

export default Filter;
