import React, { Component } from "react";
import recipeData from "../data/recipeData";
import filterData from "../data/filterData";
import RecipeCard from "./RecipeCard";
import Filter from "./Filter";

class MainContent extends Component {
  constructor() {
    super();
    this.state = {
      data: recipeData,
      filters: filterData,
      activeFilters: [],
      itemNum: 0,
    };
    this.handleOn = this.handleOn.bind(this);
    this.handleOff = this.handleOff.bind(this);
    this.handleReset = this.handleReset.bind(this);
  }

  handleOn(id) {
    let activeTag;
    this.setState((prevState) => {
      const updatedFilters = prevState.filters.map((filter) => {
        if (filter.id === id) {
          if (filter.active === false) {
            filter.active = true;
            activeTag = [filter.tag];
          }
        }
        return filter;
      });
      return {
        filters: updatedFilters,
        activeFilters: prevState.activeFilters.concat(activeTag),
      };
    });
  }

  handleOff(id) {
    let inactiveTag;
    this.setState((prevState) => {
      const updatedFilters = prevState.filters.map((filter) => {
        if (filter.id === id) {
          if (filter.active === true) {
            filter.active = false;
            inactiveTag = filter.tag;
          }
        }
        return filter;
      });
      return {
        filters: updatedFilters,
        activeFilters: prevState.activeFilters.filter((e) => e !== inactiveTag),
      };
    });
  }

  handleReset() {
    this.setState((prevState) => {
      const updatedFilters = prevState.filters.map((filter) => {
        if (filter.active === true) {
          filter.active = false;
        }
        return filter;
      });
      return {
        filters: updatedFilters,
        activeFilters: prevState.activeFilters.splice(),
      };
    });
  }

  componentDidMount() {
    let num = document.getElementsByClassName("recipe-card").length;
    this.setState({ itemNum: num });
  }

  componentDidUpdate() {
    let num2 = document.getElementsByClassName("recipe-card").length;
    if (num2 !== this.state.itemNum) {
      this.setState({ itemNum: num2 });
    }
  }

  render() {
    const filters = this.state.filters.map((filter) => {
      return (
        <Filter
          key={filter.id}
          filter={filter}
          handleOn={this.handleOn}
          handleOff={this.handleOff}
        />
      );
    });

    const mainFilters = this.state.filters.map((filter, index) =>
      filter.active ? (
        <li key={index}>
          <i
            className="fas fa-times"
            onClick={(event) => this.handleOff(filter.id)}
          ></i>
          {filter.tag}
        </li>
      ) : (
        <li
          key={index}
          style={{ height: "30px", width: "0", padding: "0", marginRight: "0" }}
        ></li>
      )
    );

    const recipes = this.state.data.map((recipe) => {
      const tags = recipe.tags;
      const filters = this.state.activeFilters;

      const intersection = tags.filter((x) => filters.includes(x));

      if (filters.length > 0) {
        if (intersection.length > 0) {
          return <RecipeCard key={recipe.id} recipe={recipe} />;
        }
      } else return <RecipeCard key={recipe.id} recipe={recipe} />;
    });

    return (
      <section>
        <div className="main-top">
          <div className="bread-crumbs">
            Home / <strong>Recipes</strong>
          </div>
          <div className="main-top-right">
            <div className="main-right">
              <h1>
                Recipes<span> - {this.state.itemNum} Items</span>
              </h1>
              <p>
                View: <i className="fas fa-th-large"></i>
                <i className="fas fa-th"></i>
              </p>
            </div>
            <div className="main-filter">
              <ul>{mainFilters}</ul>
            </div>
          </div>
        </div>
        <div className="main">
          {/* mobile */}
          <div className="mobile-filters">
            <div className="top">
              <h1>filters</h1>
              <i className="fas fa-sliders-h"></i>
            </div>
            <h2 onClick={this.handleReset}>Reset All</h2>
            <div className="mobile-filter-grid">{filters}</div>
          </div>
          {/* mobile */}
          <div className="filters">
            <div className="filters-top">
              <h1>filters</h1>
              <h2 onClick={this.handleReset}>Reset All</h2>
            </div>
            {filters}
            <div
              style={{
                background: "#e4d6b7",
                height: "1px",
                width: "100%",
                marginTop: "40px",
              }}
            ></div>
          </div>
          <div className="recipes">{recipes}</div>
        </div>
      </section>
    );
  }
}

export default MainContent;
